<template>
  <div class="register-box" v-if="config.isShow" @click.stop>
    <img class="register-dialog" src="http://service.globaltrace.com/zzz/reg-bg.png" />
    <div class="register-info">
      <div class="title">您已获得王者荣耀皮肤，请注册</div>
      <div class="title" style="margin-left:5px;">安装大家乐棋牌APP，立即领取！</div>
      <div class="mobile-input">
        <img ref="mobileImg" src="http://service.globaltrace.com/zzz/mobile-bg.png" />
        <input v-model="formData.mobile" type="number" placeholder="请输入您的手机号" />
      </div>
      <div class="code-box">
        <div class="code-input">
          <img
            src="http://service.globaltrace.com/zzz/mobile-bg.png"
            :style="{height:inputImgHeight?inputImgHeight:''}"
          />
          <input v-model="formData.code" type="text" placeholder="请输入验证码" />
        </div>
        <img class="code-btn" src="http://service.globaltrace.com/zzz/code-btn.jpg" @click="getCheckCode" />
      </div>
      <div class="register-tips">温馨提示：游戏登录密码默认手机后6位</div>
      <img class="submit" src="http://service.globaltrace.com/zzz/submit.jpg" @click="submit" />
    </div>
    <img class="close" src="http://service.globaltrace.com/zzz/close.png" @click="config.isShow=false" />
  </div>
</template>
<script>
export default {
  props: ["config"],
  data() {
    return {
      formData: {
        mobile: null,
        code: null
      },
      inputImgHeight: null
    };
  },
  mounted() {
    setTimeout(() => {
      this.inputImgHeight = this.$refs.mobileImg.offsetHeight-7 + "px";
    }, 100);
    this.$eventBus.$on("showRegister", () => (this.config.isShow = true));
  },
  methods: {
    getCheckCode() {
      if (this.formRule()) {
        //获取验证码
        this.$eventBus.$emit("showImgCode", this.formData.mobile);
      }
    },
    submit() {
      if (this.formRule("submit")) {
        //提交
        let guid = this.$store.getters.imgCodeInfo.guid;
        let ua = window.navigator.userAgent;
        let platForm = /iphone|ipad/i.test(ua) ? "ios" : "android";
        this.$http({
          url: this.$api.userRegister,
          method: "post",
          data: {
            user_name: this.formData.mobile,
            password: this.formData.mobile.slice(-6),
            guid: guid,
            mobile: this.formData.mobile,
            verify_code: this.formData.code
          }
        }).then(res => {
          console.log(res.data);
          this.$msg.hideLoading();
          if (res.data.S == "200") {
            // this.$msg.success(res.data.D);
            this.$store.dispatch('changeLotteryStatus',false)
            window.location.href =
              "http://ca.zglzgc.cn/download.html?" +
              platForm +
              "&register&phone=" +
              this.formData.mobile;
            this.config.isShow = false;
          } else {
            this.$msg.fail(res.data.D, 2000);
          }
        });
      }
    },
    formRule(type = "default") {
      let state = 1;
      let mobileReg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
      let { mobile, code } = this.formData;

      if (!mobile) {
        this.$msg.fail("请输入手机号");
        state = 0;
      } else if (!mobileReg.test(mobile)) {
        this.$msg.fail("请输入正确的手机号");
        state = 0;
      } else if (type == "submit" && !code) {
        this.$msg.fail("请输入验证码");
        state = 0;
      } else if (type == "submit" && code) {
        this.$msg.loading("注册中...");
        state = 1;
      }
      return state;
    }
  }
};
</script>
<style scoped lang="stylus">
input {
  width: 100%;
  font-size: 14px;
  position: absolute;
  left: 0;
  top: -4%;
  height: 100%;
  @media screen and (min-device-width: 900px) {
    font-size: 32px;
  }

  color: #fff;

  &::placeholder {
    color: #ddd;
  }
}

.register-box {
  position: absolute;
  left: 50%;
  top: 20%;
  transform: translateX(-50%);
  width: 80%;
}

.register-dialog {
  width: 100%;
  display: block;
  margin: 0 auto;
}

.register-info {
  width: 100%;
  position: absolute;
  left: 0;
  top: 15%;

  .title {
    width: 100%;
    padding: 0 10%;
    font-size: 15px;
     @media screen and (min-device-width: 900px) {
     font-size: 30px;
     }
    text-align: center;
    color: #f8d15b;
  }

  .mobile-input {
    width: 100%;
    padding: 0 8%;
    margin-top: 3%;

    img {
      width: 100%;
    }

    input {
      padding: 0 12%;
    }
  }

  .code-box {
    width: 100%;
    padding: 0 8%;
    margin-top: 3%;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .code-input {
      flex: 1;
      margin-right: 15px;

      img {
        width: 100%;
      }

      input {
        padding: 0 7%;
      }
    }

    .code-btn {
      width: calc((160px / 2));
      
      @media screen and (min-device-width: 900px) {
        width: calc((330px / 2));
      }
    }
  }

  .register-tips {
    color: red;
    font-size: 12px;
    @media screen and (min-device-width: 900px) {
      font-size: 28px;
    }
    text-align: center;
    width: 100%;
    margin-top:2%;
  }

  .submit {
    width: 100px;
    display: block;
    margin: 5px auto 0;

    @media screen and (min-device-width: 900px) {
        width:  230px; margin: 15px auto 0;
      }
  }
}

.close {
  position: absolute;
  right: 10px;
  top: 25px;
  width: 15px;
  @media screen and (min-device-width: 900px) {
    width: 30px;right: 20px;
    top: 50px;
  }
}
</style>

