<template>
  <div class="img-code" v-if="imgCodeMobile">
    <div class="title">
      <img src="http://service.globaltrace.com/zzz/img-code-title.jpg" />
      请先输入验证码
    </div>
    <div class="img-code-info">
      <div class="img-code-input">
        <input v-model="imgCodeVal" type="text" placeholder="请输入验证码" />
        <div></div>
      </div>
      <img :src="imgCodeInfo.url" @click="changeImgCode" />
    </div>
    <div class="submit" @click="submit">发送短信</div>
    <img class="close" src="http://service.globaltrace.com/zzz/close.png" @click="imgCodeMobile=''" />
  </div>
</template>
<script>
export default {
  props: ["config"],
  data() {
    return {
      imgCodeVal: "",
      imgCodeMobile: "",
      imgCodeInfo: {}
    };
  },
  mounted() {
    this.changeImgCode();
    this.$eventBus.$on("showImgCode", mobile => {
      this.imgCodeMobile = mobile;
    });
  },
  methods: {
    changeImgCode() {
      this.imgCodeInfo = this.getImgCode();
      this.$store.dispatch("changeImgCodeInfo", this.imgCodeInfo);
    },
    getImgCode() {
      let version = Math.random();
      let guid = this.randomRangeId(10);
      let bgColor = "%23b69a75";
      let url =
        this.$api.host +
        this.$api.getImgCode +
        "?v=" +
        version +
        "&bcolor=" +
        bgColor +
        "&guid=" +
        guid;
      return { version, guid, bgColor, url };
    },
    submit() {
      this.$msg.loading("发送中...");
      this.$http({
        url: this.$api.getCode,
        method: "post",
        data: {
          code: this.imgCodeVal,
          guid: this.imgCodeInfo.guid,
          mobile: this.imgCodeMobile
        }
      })
        .then(res => {
          this.$msg.hideLoading();
          console.log(res.data);
          if (res.data.S == 200) {
            this.$msg.success("验证码发送成功");
            this.imgCodeMobile = "";
          } else {
            this.changeImgCode();
            this.$msg.fail(res.data.D, 2000);
          }
        })
    },
    randomRangeId(num) {
      var returnStr = "",
        charStr =
          "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      for (var i = 0; i < num; i++) {
        var index = Math.round(Math.random() * (charStr.length - 1));
        returnStr += charStr.substring(index, index + 1);
      }
      return returnStr + new Date().getTime();
    }
  }
};
</script>
<style scoped lang="stylus">
.img-code {
  width: 80%;
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translateX(-50%);
  background: #221f1e;
  border-radius: 4px;
  overflow: hidden;
}

.title {
  width: 100%;
  text-align: center;
  height: 30px;
  color: #d0ba8c;
  line-height: 32px;
  letter-spacing: 1px;
  font-size: 16px;
  @media screen and (min-device-width: 900px) {
    height: 80px;
    line-height: 80px;font-size: 32px;
  }

  img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    z-index: -1;
  }
}

.close {
  position: absolute;
  right: 8px;
  top: 10px;
  width: 15px;
  @media screen and (min-device-width: 900px) {
    right: 15px;
    top: 25px;
    width: 30px;
  }
}

.img-code-info {
  width: 100%;
  padding: 0 10px;
  margin:10px 0;
  display: flex;
  align-items: center;
  height: 50px;
  @media screen and (min-device-width: 900px) {
    height: 150px;
    padding: 0 20px;
    margin: 10px 0 0;
  }
  .img-code-input {
    flex: 1;
    height: 80%;
    margin-right: 3%;
    @media screen and (min-device-width: 900px) {
      height: 60%;
    }
    div {
      width: 100%;
      height: 100%;
      border: 1px solid #b3a680;
      border-radius: 4px;
    }

    input {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      padding: 0 14px;
      font-size: 16px;
      z-index: 1;
      color: #fff;
      @media screen and (min-device-width: 900px) {
        font-size: 32px;padding: 0 20px;
      }
      background transparent {
        &::placeholder {
          color: #ddd;
        }
      }
    }
  }

  img {
    width: 80px;
    @media screen and (min-device-width: 900px) {
      width: 176px;
    }
  }
}

.submit {
  width: calc(100% - 80px);
  padding: 5px 0;
  margin: 10px auto 20px;
  border-radius: 50px;
  text-align: center;
  color: #fff;
  font-size: 16px;
  background: #b69a75;

  @media screen and (min-device-width: 900px) {
    font-size: 32px;padding: 20px 0;
    margin: 5px auto 10px;
  }
}
</style>


