<template>
  <div class="lottery">
    <img :class="[isRotate?'wheelAnimation':'','wheel']" src="http://service.globaltrace.com/zzz/lottery_img.png" />
    <img
      class="pointer"
      :style="{'transform':styleTransform,'transition':'transform 6s ease-in-out'}"
      src="http://service.globaltrace.com/zzz/pointer.png"
      @click="startLottery"
    />
    
  </div>
</template>
<script>
export default {
  data() {
    return {
      styleTransform: "rotate(0deg)", //初始旋转角度\
      rotateStartAngle: 0,
      isRotate:true,
      isDisabled:false,
    };
  },
  mounted() {},
  computed:{
    isLottery(){
      return this.$store.getters.lotteryStatus
    }
  },
  methods: {
    startLottery() {
      if (this.isLottery && !this.isDisabled) {
        this.isRotate=false
        this.getLottery();
      }
    },
    getLottery() {

      this.isDisabled=true
      let fullCircleCount = 10 * 360; //转几圈
      let resultIndex = this.randomResult(); //得奖结果
      //初始旋转角度 每次抽奖重新注册
      let rotateAngle =
        this.rotateStartAngle +
        fullCircleCount +
        30 * resultIndex -
        (this.rotateStartAngle % 360); //设置当前旋转的角度  转圈数 + 当前结果的角度 - 上次旋转的角度
      this.styleTransform = "rotate(" + rotateAngle + "deg)"; //给指针赋值 当前旋转的角度
      this.rotateStartAngle = rotateAngle; // 设置下次旋转的起始度数
      setTimeout(() => {
        this.endLottery();
      }, 6500);
    },
    endLottery() {
      this.isDisabled=false
      this.$eventBus.$emit("showRegister");
    },
    randomResult() {
      let arr = [11, 2, 4, 9];
      let index = Math.floor(Math.random() * arr.length);
      return arr[index];
    }
  }
};
</script>
<style scoped lang="stylus">
.lottery {
  width: 100%;

  .wheel {
    display: block;
    margin: 0 auto;
    width: 90%;
    transform :rotate(0);

  }
  .wheelAnimation{
    animation-duration 25s;
    animation-name wheelRotate;
    animation-iteration-count infinite;
    animation-timing-function linear;
  }
  .pointer {
    position: absolute;
    left: 31%;
    top: 20%;
    width: 37%;
    transform-origin: 48% 60%;
  }
}
@keyframes wheelRotate{
  form{
    transform :rotate(0)
  }
  to{   transform :rotate(360deg)}
}
</style>

